import React , { useEffect }from 'react';
import { BrowserRouter, Routes, Route , useLocation} from 'react-router-dom'
import MainPage from './pages/Main.jsx';
import NewsEvents from './pages/NewsEvents.jsx';
import NewsDetails from './components/NewsDetails.jsx';
import './App.css';
import CaseStudies from './pages/CaseStudies.jsx';
import CaseDetails from './components/CaseDetails.jsx';
import TechInsight from './pages/TechInsight.jsx';
import TechInsightDetails from './components/TechInsightDetails.jsx';
import EcosystemsAlliances from './pages/Ecosystems.jsx';
import ContactPage from './pages/Contact.jsx';
import AboutPage from './pages/About.jsx';
import ServicePage from './pages/Service.jsx'
import AwsPages from './pages/microsite/Aws.jsx';
import GoogleCloudPages from './pages/microsite/GoogleCloud.jsx';
import MsAzurePage from './pages/microsite/MsAzure.jsx';
import EngMainPage from './englishVer/EnglishMain.jsx';
import EngAboutPage from './englishVer/EnglishAbout.jsx';
import EngServicePage from './englishVer/EnglishService.jsx';
import EngContactPage from './englishVer/EnglishContact.jsx';
import EngEcosystemsAlliances from './englishVer/EnglishEcosystems.jsx';
import EngCaseStudies from './englishVer/EnglishCaseStudies.jsx';
import AwsDetails from './components/AwsDetails.jsx';
import NavBar from './components/Nav.jsx';
import AsideNav from './components/Aside.jsx';
import Footer from './components/Footer.jsx';
// import CloudXperPages from './pages/microsite/CloudXper.jsx';



const ScrollToTop = () => {
  
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
};

const App = () => {


  return (
   <BrowserRouter basename={process.env.PUBLIC_URL}>
     <ScrollToTop />
     <NavBar />
     <AsideNav />
     <Routes>
       {/* <Route path='/' element={<MainPage/>}></Route>*/}
        <Route path='/about' element={<AboutPage />}></Route>
        <Route path="/serviceOfferings" element={<ServicePage />}></Route>
        <Route path='/newsEvent' element={<NewsEvents/>}></Route>
        <Route path='/newsEvent/:id' element={<NewsDetails/>}></Route>
        <Route path='/caseStudies' element={<CaseStudies />}></Route>
        <Route path='/caseStudies/:id' element={<CaseDetails/>}></Route>
        <Route path='/techInsight' element={<TechInsight/>}></Route>
        <Route path='/techInsight/:id' element={<TechInsightDetails/>}></Route>
        <Route path='/ecosystemsAlliances' element={<EcosystemsAlliances/>}></Route>
        <Route path="/contact" element={<ContactPage />}></Route>
        <Route path="/aws" element={<AwsPages />}></Route>
        <Route path="/googleCloud" element={<GoogleCloudPages />}></Route>
        <Route path="/msAzure" element={<MsAzurePage />}></Route>
        <Route path="/" element={<EngMainPage/>}></Route>
        <Route path="/englishAbout" element={<EngAboutPage />}></Route>
        <Route path="/englishService" element={<EngServicePage />}></Route>
        <Route path="/englishContact" element={<EngContactPage />}></Route>
        <Route path="/englishEcosystemsAlliance" element={<EngEcosystemsAlliances />}></Route>
        <Route path="/englishCaseStudies" element={<EngCaseStudies />}></Route>
        <Route path="/awsDetails" element={<AwsDetails />}></Route>
     </Routes>
    <Footer/>
  </BrowserRouter>
  );
}

export default App;
