import React , { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import asideDB from '../json/Aside.json'
import asideImg01 from '../assets/imgs/aside/lgCnsCloud.png';
import asideImg02 from '../assets/imgs/aside/awsLogo.png';
import asideImg03 from '../assets/imgs/aside/googleCloud.png';
import asideImg04 from '../assets/imgs/aside/msAzure.png';
// import asideImg05 from '../assets/imgs/aside/cloudXper.png';

const AsideNav = ( ) => {

   const [ hrefName, setHrefName ] = useState();
   const { pathname } = useLocation();

   useEffect(() => {  

      if(hrefName === undefined ||  pathname === undefined){
         return setHrefName('/');

         }else if(pathname.includes('/caseStudies/:details')){
            return setHrefName('/');
         }else if(pathname.includes('/newsEvent/:details')){
            return setHrefName('/');
         }else if(pathname.includes('/techInsight/:details')){
            return setHrefName('/');
         }else if(pathname.includes('/englishMain')){
            return setHrefName('/');
         }else if(pathname.includes('/englishAbout')){
            return setHrefName('/');
         }else if(pathname.includes('/englishService')){
            return setHrefName('/');
         }else if(pathname.includes('/englishContact')){
            return  setHrefName('/');
         }else if(pathname.includes('/englishEcosystemsAlliance')){
            return  setHrefName('/');
         }else if(pathname.includes('/englishCaseStudies')){
            return  setHrefName('/');
         }
         else if(pathname.includes('/awsDetails')){
            return setHrefName('/aws');
         }
         setHrefName(pathname);
         console.log(hrefName , 'aside');

   } , [hrefName, pathname])
 

    return(
      <aside>
       <div className="asideNav">
          <ul className="asideNavList">
             <li><Link to='/' className={ asideDB.home.filter( names => names === hrefName).map((v) => v ? "active" : "")} style={{ backgroundImage: `url(${asideImg01})` }}></Link></li>
             <li><Link to='/aws' className={ asideDB.aws.filter( names => names === hrefName).map((v) => v ? "active" : "")} style={{ backgroundImage: `url(${asideImg02})` }}></Link></li>
             <li><Link to='/googleCloud' className={ asideDB.googleCloud.filter( names => names === hrefName).map((v) => v ? "active" : "")} style={{ backgroundImage: `url(${asideImg03})` }}></Link></li>
             <li><Link to='/msAzure' className={ asideDB.msAzure.filter( names => names === hrefName).map((v) => v ? "active" : "")} style={{ backgroundImage: `url(${asideImg04})` }}></Link></li>
          </ul>
       </div>
       </aside>
    )
}

export default AsideNav;
