import React , { useState, useEffect } from 'react';
import { Link } from 'react-router-dom'; 
import searchImg from '../assets/imgs/caseStudies/search.png';
import resetImg from '../assets/imgs/caseStudies/restart.png';
import arrowImg from '../assets/imgs/caseStudies/arrowNext.png';
import caseStudiesDB from '../json/englishVer/EnglishCaseStudies.json';
import FixedButton from '../components/FixedButton.jsx';
import CaseSkeletonPage from '../components/skeletons/CaseSkeletonPage.jsx';
import CaseArrowDownImg from '../assets/imgs/caseStudies/caseArrowDown.png';
import CaseArrowUpImg from '../assets/imgs/caseStudies/caseArrowUp.png';
import enCaseFilterImg from '../assets/imgs/englishVer/englishCaseStudies/enCaseFilter.png';
import enCaseBackImg from '../assets/imgs/englishVer/englishCaseStudies/enCaseBackground.png';

const EngCaseStudies = () => {
     
    const [ reset, setReset ] = useState(false);
    const [loadingPage, setLoadingPage] = useState();
    const [openCaseTitles, setOpenCaseTitles] = useState(false);
    const [selectedCategories, setSelectedCategories] = useState([]);
    const [flipBtns, setflipBtns] = useState(false);
    const [categoryListOpen , setCategoryListOpen] = useState(false);

    const resetClickEvet = () => {

      if(reset === true){ 
        setReset(false)
        setSelectedCategories([]);

      }else{
      setSelectedCategories([]);
      setReset(true);
    } 
   }

   const handleCategoryClick = (category) => {
    
    if (selectedCategories.includes(category)) {
      
      setSelectedCategories(selectedCategories.filter(item => item !== category));

    } else {
      setSelectedCategories([...selectedCategories, category]);
    }
  };


   const caseLoadingEvet = () => {

      setTimeout(() => {

        setLoadingPage(true)

      },1000) 
   }

  const caseFlipClickEvet = () => {

     if(flipBtns === true){

       if(categoryListOpen === true){

         return setCategoryListOpen(false);

       }
       setCategoryListOpen(true);

     }
   

  }
  const caseResizeEvet = () => {

     if(window.innerWidth <= 1024){
       return setflipBtns(true);

     }else{
       return setflipBtns(false),
       setCategoryListOpen(false);
     }
  }

    useEffect(() => {

       caseLoadingEvet();

    },[reset, loadingPage , selectedCategories])
    
    useEffect(() => {

      window.addEventListener("load", setOpenCaseTitles(true), caseResizeEvet());
      window.addEventListener("resize" , caseResizeEvet);

       return() => {
         
          window.addEventListener("load", setOpenCaseTitles(true), caseResizeEvet())
          window.addEventListener("resize" , caseResizeEvet);
       }

    },[flipBtns, openCaseTitles])



     return(
        <div id="caseStudies">
             <div className="caseStudiesMain">
                 <div className="caseStudiesTitleboxs">
                   <h5 className={ openCaseTitles ? "active" : ""}>Case Studies</h5>
                   <div className="inputBoxs">
                   <input type="search"/>
                   <button style={{backgroundImage:`url(${searchImg})`}}></button>
                   </div>
                 </div>
                 <section className="caseStudiesContents">
                 <div className="categoryBoxies">
                    <div className="titleBoxs">
                       <span className="casefilterList" style={{backgroundImage : `url(${enCaseFilterImg})`}}></span>
                       <button style={{backgroundImage : `url(${resetImg})`}} type="button" onClick={resetClickEvet} className="CaseResetBtn enCaseResetBtn"></button>
                       <button style={categoryListOpen ? {backgroundImage : `url(${CaseArrowDownImg})`} :  {backgroundImage : `url(${CaseArrowUpImg })`}} onClick={caseFlipClickEvet} className={flipBtns ? "CaseFlipBtn active" : "CaseFlipBtn"} ></button>
                    </div>
                    <div className="categoryContents">
                       <ul className={categoryListOpen ? "checkLists active" : "checkLists"}>
                        <div className="caseCspCategories">
                        {caseStudiesDB.csp.map((v,i) => (
                         <li key={'caseCsp' + i}>
                          <span className={ selectedCategories.includes(v) ? 'selected' : ''} onClick={() => handleCategoryClick(v)}>{v}</span>
                          </li>        
                       ))}
                       </div>
                       <div className="caseOtherCategories">
                       {caseStudiesDB.category.map((v,i) => (
                         <li key={'caseCategories' + i}>
                          <span className={ selectedCategories.includes(v) ? 'selected' : ''} onClick={() => handleCategoryClick(v)}>{v}</span>
                          </li>        
                       ))}
                       </div>
                       </ul>
                    </div>
                  </div>
                  <div className="categoryListBoxies">
                    <div className="resultCount">
                      <h3><span style={{paddingRight : "3px"}}>13</span>search results</h3>
                    </div>
                    <div className="resultDatas">
                    {loadingPage && 
                      caseStudiesDB.list.map((v,i) => (
                        <ul key={'caseStudiesDatas' + i}>
                          <Link to={`/caseStudies/:details${v.id}`}>
                          <li>
                          <div className="categoriesContent">
                              <div className="categoryCont">
                                <div className="bigCategoryWrappers">
                                <h5>{v.csp}</h5>
                                <p>{v.company}</p>
                                </div>
                                <div className="categoryWrappers">
                                {v.categories.map((arr, indx) => (
                                <span key={'checkCatgory' + indx}>{arr}</span>
                                ))}
                                </div>
                              </div>
                              <button style={{backgroundImage : `url(${arrowImg})`}} type="button"></button>
                          </div>
                          <div className="listInfoBoxies">
                            <h1>{v.title}</h1>
                            <p>{v.contents}</p>
                          </div>
                          <div className="listImgs">
                              {v.img ? (<img src={`./assets/imgs/caseStudies/${v.img}`} alt={v.img} />) : (<img src={enCaseBackImg} alt={'enCaseDefaultImg'} />)}
                              {/* <img src={`./assets/imgs/caseStudies/${v.img}`} alt={v.img} /> */}
                          </div>
                          </li>
                          </Link>
                        </ul>
                      ))
                      }
                  {!loadingPage && 
                   caseStudiesDB.list.map((v,idx) => (
                     <CaseSkeletonPage key={'caseSkeletons' + idx} />
                   ))
                  }
                    </div>
                  </div>
                    </section>
            </div>
            <FixedButton />
        </div>
        
     )
}

export default EngCaseStudies;