import React , { useState, useEffect } from 'react';
import { Link , useLocation } from 'react-router-dom';
import homeImg from '../assets/imgs/micrositeNav/home.png'
import microSiteDB from '../json/MicrositeNav.json';
import microLanguageImg from '../assets/imgs/micrositeNav/microLanguage.png';
import moMicroHomeImg from '../assets/imgs/micrositeNav/mo_microHome.png';
import moMicroMenuImg from '../assets/imgs/micrositeNav/mo_microMenu.png';
import moMicroWhiteHomeImg from '../assets/imgs/micrositeNav/mo_microWhiteHome.png';
import moMicroCloseMenuImg from '../assets/imgs/micrositeNav/mo_microClose.png';
import moMicroAwsImg from '../assets/imgs/micrositeNav/mo_microAws.png';
import moMicroGoogleImg from '../assets/imgs/micrositeNav/mo_microGoogleCloud.png';
import moMicroMsAzureImg from '../assets/imgs/micrositeNav/mo_microMsAzure.png';


const MicroSiteNav = ( { navList }) => {

   const { pathname } = useLocation();
   const [ microLanguage, setMicroLanguage] = useState('KR');
   const [ nowPath, setNowPath ] = useState();
   const [ microNavHeight, setMicroNavHeight] = useState(false);


   const microLanguageEvet = () => {

       if(microLanguage === 'KR'){
         setMicroLanguage('EN')
       }else{
         setMicroLanguage('KR')
       }
   }

   const mobileNavListOpen = () => {

     if(microNavHeight === true){

        setMicroNavHeight(false)
      }else{
        setMicroNavHeight(true)
      }
       
   }

   const micrositeNavEvet = () => {
        setMicroNavHeight(false);
   }

   useEffect(() => {

      if(navList){
        setNowPath(navList)
      }

   },[pathname , microLanguage, nowPath])

   useEffect(() => {
      
     
   },[microNavHeight])


    return(
       
      <div className={ microNavHeight ? "micrositeNav active" : "micrositeNav"}>
          <ul className="microMainNavList" onClick={mobileNavListOpen}>
           { nowPath === 'aws' ? (microSiteDB.aws.map((v,i)=>(
               <li key={'microsite' + i}><Link to={v.href}>{v.title}</Link><em></em></li>
            ))) : ""
           } 
            { nowPath === 'googleCloud' ? (microSiteDB.googleCloud.map((v,i)=>(
               <li key={'microsite' + i}><Link to={v.href}>{v.title}</Link><em></em></li>
            ))) : ""
           }
           { nowPath === 'msAzure' ? (microSiteDB.msAzure.map((v,i)=>(
            <li key={'microsite' + i}><Link to={v.href}>{v.title}</Link><em></em></li>
            ))) : ""
            } 
          </ul>
          <ul className="microSubNavList" style={ nowPath === 'aws' ? {borderRadius : "40px"} : {}} onClick={mobileNavListOpen}>
            {nowPath === 'aws'? (
              <>
                <li><button style={{backgroundImage : `url(${microLanguageImg})`}} onClick={microLanguageEvet}>{microLanguage}</button></li>
                <li style={{paddingLeft : "0px"}}><Link to='/aws'><img src={homeImg} alt="homeImg" /></Link></li>
                </>
             ) : ''}
              {nowPath === 'googleCloud'? (
              <>
                 <li><Link to='/googleCloud'><img src={homeImg} alt="homeImg" /></Link></li>
                </>
             ) : ''}
              {nowPath === 'msAzure'? (
              <>
                 <li><Link to='/msAzure'><img src={homeImg} alt="homeImg" /></Link></li>
                </>
             ) : ''}
          </ul>
          <div className={microNavHeight ? "mobileMicroNavList active" : "mobileMicroNavList"}>
            <div className="moMicroBasicBoxs">
                <ul className='moLogos' onClick={micrositeNavEvet}>
                  { nowPath === 'aws' ? (
                     <li><Link to='/aws'><img src={moMicroAwsImg} alt='awslogos'></img></Link></li>
                  ) : ''}
                  { nowPath === 'googleCloud' ? (
                     <li><Link to='/googleCloud'><img src={moMicroGoogleImg} alt='googlelogos'></img></Link></li>
                  ) : ''}
                  { nowPath === 'msAzure' ? (
                     <li><Link to='/msAzure'><img src={moMicroMsAzureImg} alt='msAzurelogos' style={{height : "35px"}}></img></Link></li>
                  ) : ''}
                </ul>
                <ul className='moMenuSubList'>
                   { nowPath === 'aws' ? (
                    <>
                      <li onClick={micrositeNavEvet}><Link to='/aws' className='homeBtn' style={ microNavHeight ? {backgroundImage : `url(${moMicroWhiteHomeImg})`} : {backgroundImage : `url(${moMicroHomeImg})`}}></Link></li>
                      <li><button className='menuBtn' style={ microNavHeight ? {backgroundImage : `url(${moMicroCloseMenuImg})`} : {backgroundImage : `url(${moMicroMenuImg})`}} onClick={mobileNavListOpen}></button></li>
                      </>
                   ) : ''}
                   { nowPath === 'googleCloud' ? (
                    <>
                      <li onClick={micrositeNavEvet}><Link to='/googleCloud' className='homeBtn' style={ microNavHeight ? {backgroundImage : `url(${moMicroWhiteHomeImg})`} : {backgroundImage : `url(${moMicroHomeImg})`}}></Link></li>
                      <li><button className='menuBtn' style={ microNavHeight ? {backgroundImage : `url(${moMicroCloseMenuImg})`} : {backgroundImage : `url(${moMicroMenuImg})`}} onClick={mobileNavListOpen}></button></li>
                      </>
                   ) : ''}
                    { nowPath === 'msAzure' ? (
                    <>
                      <li onClick={micrositeNavEvet}><Link to='/msAzure' className='homeBtn' style={ microNavHeight ? {backgroundImage : `url(${moMicroWhiteHomeImg})`} : {backgroundImage : `url(${moMicroHomeImg})`}}></Link></li>
                      <li><button className='menuBtn' style={ microNavHeight ? {backgroundImage : `url(${moMicroCloseMenuImg})`} : {backgroundImage : `url(${moMicroMenuImg})`}} onClick={mobileNavListOpen}></button></li>
                      </>
                   ) : ''}
                </ul>
              </div>
              <ul className={microNavHeight ? "moMicroMainNavList active" : "moMicroMainNavList"} onClick={mobileNavListOpen}>
              { nowPath === 'aws' ? (microSiteDB.aws.map((v,i)=>(
               <li key={'microsite' + i}><Link to={v.href}>{v.title}</Link><em></em></li>
                ))) : ""
              } 
               { nowPath === 'googleCloud' ? (microSiteDB.googleCloud.map((v,i)=>(
               <li key={'microsite' + i}><Link to={v.href}>{v.title}</Link><em></em></li>
                ))) : ""
              } 
               { nowPath === 'msAzure' ? (microSiteDB.msAzure.map((v,i)=>(
               <li key={'microsite' + i}><Link to={v.href}>{v.title}</Link><em></em></li>
                ))) : ""
              } 
              </ul>
          </div>
      </div>
    )
}

export default MicroSiteNav;