import React , { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import newestImg from '../assets/imgs/techInsight/newest.png';
import resetImg from '../assets/imgs/techInsight/restart.png';
import filterImg from '../assets/imgs/techInsight/filter.png';
import arrowDown from '../assets/imgs/techInsight/arrowDown.png';
import arrowUpImg from '../assets/imgs/techInsight/changeArrow.png';
import techInsightDB from '../json/pages/TechInsight.json';
import TechSkeletonPage from '../components/skeletons/TechSkeletonPage.jsx';
import FixedButton from '../components/FixedButton.jsx';
import techBackImg from '../assets/imgs/techInsight/techBackground.png';

const TechInsight = () => {


    const [techInsightNewest , setTechInsightNewest] = useState(false)
    const [techInsighReset , setTechInsighReset] = useState(false);
    const [techInsighMoreBtn , setTechInsighMoreBtn] = useState(false);
    const [techLoading  , setTechLoading] = useState();
    const [openTechTitles  , setOpenTechTitles] = useState();
    const [techSelectedRadio, setTechSelectedRadio] = useState(null);
    const [techSelectedCategory, setTechSelectedCategory] = useState([]);


    const techInsightNewestClickEvet = () => {
       
      if(techInsightNewest === true){
         setTechInsightNewest(false)
      }else{
         setTechInsightNewest(true)
      }
 }

      const techInsightResetClickEvet = () => {

            if(techInsighReset === true){ 
            setTechInsighReset(false)
            setTechSelectedCategory([]);

            }else{
            setTechSelectedCategory([]);
            setTechInsighReset(true);
         } 
      }

   const techHandleRadioClick = (category) => {
      setTechSelectedRadio(category);
      };

      
   const techHandleCategoryClick = (category) => {
   
      if (techSelectedCategory.includes(category)) {
         
         setTechSelectedCategory(techSelectedCategory.filter(item => item !== category));
   
      } else {
         setTechSelectedCategory([...techSelectedCategory, category]);
      }
      };


      const techInsightMoreBtnClickEvet = (e) => {

         const thisTarget = e.target;

         if(techInsighMoreBtn === true){ 
         setTechInsighMoreBtn(false)
         thisTarget.textContent = ` `;

         }else{
         setTechInsighMoreBtn(true);
         thisTarget.textContent = ` `;
      } 
   }

   const techLoadingEvet = () => {

      setTimeout(() => {

         setTechLoading(true);

      },1000)
  }



 useEffect(() => {

   techLoadingEvet();

 },[techSelectedRadio, techSelectedCategory,techInsightNewest,techInsighReset, techInsighMoreBtn, techLoading])

 useEffect(() => {

   window.addEventListener("load", setOpenTechTitles(true))

},[])

     return(
        <div id="techInsight">
             <div className="techInsightMain">
                 <div className="techInsightTitleboxs">
                   <h5 className={ openTechTitles ? "active" : ""}>Tech Insight</h5>
                 </div>
                 <section className="techInsightContents">
                     <div className="techInsightCategoryBoxies">
                         <ul className="categorys">
                           <span className="filterList" style={{backgroundImage : `url(${filterImg})`}}></span>
                            { techInsightDB.category.map((v,i) => (
                              <li key={'techInsightCategory' + i}>
                                    <span className={techSelectedRadio === v ? 'selected' : ''} onClick={() => techHandleRadioClick(v)}>{v}</span>
                                 </li>
                           ))}
                         </ul>
                        <div className="techInsightSubCategoryBoxies">
                           <div className="techInsightSubCategoryCheckBoxs">
                              <ul className={techInsighMoreBtn ? "checkLists active" : "checkLists"}>
                                 {techInsightDB.subCategory.map((v,i) => (
                                    <li key={'checkList' + i}>
                                      <span className={ techSelectedCategory.includes(v) ? 'selected' : ''} onClick={() => techHandleCategoryClick(v)}>{v}</span>
                                    </li>        
                                 ))}
                           </ul>
                           <div className="resetBtnBoxs"><button className='resetBtn' style={{backgroundImage : `url(${resetImg})`}} onClick={techInsightResetClickEvet} type="button"></button></div>
                          </div>
                          <div className="moreDatasBtn"><button style={techInsighMoreBtn ?  {backgroundImage : `url(${arrowUpImg})`} : {backgroundImage : `url(${arrowDown})`}} onClick={techInsightMoreBtnClickEvet}> </button></div>
                         </div>
                     </div>
                     <div className="techInsightResultBoxies">
                         <div className="techInsightResultCount">
                            <h3><span>13</span> </h3>
                            { techInsightNewest ? <button style={{ backgroundImage: `url(${newestImg})`}} className="active" onClick={techInsightNewestClickEvet}></button> : 
                             <button style={{ backgroundImage: `url(${newestImg})`}} onClick={techInsightNewestClickEvet}></button> }
                         </div>
            
                         <div className="techInsightResultDatas">
                          {techLoading && techInsightDB.contents.map((v, i) => (
                              <ul key={'techInsightDatas'+i}>
                              <Link to={`/techInsight/:details${v.id}`}>
                                 <li>
                                    <div className="imgBoxs">{v.img ? (<img src={'./assets/imgs/techInsight/'+v.img} alt={'techInsightDataImg'+ i} />) : (<img src={techBackImg} alt={'techDefaultImg'} />)}</div>
                                    {/* <div className="imgBoxs"><img src={'./assets/imgs/techInsight/'+v.img} alt={'techInsightDataImg'+ i} /></div> */}
                                    <div className="titleBoxs">
                                       <div className="titleSubBoxs">
                                          <div className="contentsSubInfo">
                                             <p>{v.writer}</p>
                                             <div className="onlyCategories">
                                                {v.categories.map((arr, indx) => (
                                                   <span key={'checkCatgories' + indx}>{arr}</span>
                                                ))}
                                             </div>
                                          </div>
                                          <h2>{v.title}</h2>
                                       </div>
                                       <div className="titleBigBoxs">
                                          <p>{v.company}</p><span>{v.dates}</span>
                                       </div>
                                    </div>
                              </li>
                              </Link> 
                           </ul>
                           ))
                          }  
                       {!techLoading && techInsightDB.contents.map((v,idx) => ( <TechSkeletonPage key={'Techskeleton' + idx} />))}
                         </div>
                     </div>
                 </section>
              </div>
            <FixedButton />
        </div>

     )
}

export default TechInsight;